'use client';

import { useState, useEffect } from 'react';

interface Review {
  id: string;
  comment: string | null;
  rating: number;
  user: {
    name: string | null;
  };
  product: {
    name: string | null;
  } | null;
  employee: {
    name: string | null;
  } | null;
}

const StarRating = ({ rating }: { rating: number }) => (
  <div className="flex items-center">
    {[...Array(5)].map((_, index) => (
      <svg
        key={index}
        className={`w-4 h-4 ${index < rating ? 'text-yellow-400' : 'text-gray-300'}`}
        fill="currentColor"
        viewBox="0 0 20 20"
      >
        <path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
      </svg>
    ))}
  </div>
);

const ReviewCard = ({ review }: { review: Review }) => (
  <div className="bg-white rounded-lg shadow-lg p-6 mx-4 w-80 flex-shrink-0">
    <div className="flex items-center mb-4">
      <div className="w-12 h-12 rounded-full bg-indigo-500 flex items-center justify-center text-white font-bold text-xl">
        {review.user?.name?.charAt(0) || 'A'}
      </div>
      <div className="ml-4">
        <p className="font-bold text-gray-800">{review.user?.name || 'Anonymous'}</p>
        <p className="text-sm text-gray-500">
          Reviewed {review.product?.name || review.employee?.name || 'a service'}
        </p>
      </div>
    </div>
    <StarRating rating={review.rating} />
    <p className="text-gray-600 mt-4 italic">"{review.comment}"</p>
  </div>
);

export default function ReviewSlideshow() {
  const [reviews, setReviews] = useState<Review[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchReviews = async () => {
      try {
        const res = await fetch('/api/reviews');
        if (res.ok) {
          const data = await res.json();
          // Filter out reviews with no comments and duplicate for a seamless loop
          const filteredReviews = data.filter((r: Review) => r.comment);
          setReviews([...filteredReviews, ...filteredReviews]);
        }
      } catch (error) {
        console.error('Failed to fetch reviews:', error);
      } finally {
        setLoading(false);
      }
    };
    fetchReviews();
  }, []);

  if (loading) {
    return <div className="text-center p-10">Loading reviews...</div>;
  }

  return (
    <div className="bg-gray-50 py-12">
      <div className="container mx-auto text-center">
        <h2 className="text-3xl font-bold text-gray-800 mb-2">What Our Customers Say</h2>
        <p className="text-lg text-gray-600 mb-8">Real feedback from our valued customers.</p>
      </div>
      <div className="w-full overflow-hidden">
        <div className="flex animate-scroll group">
          {reviews.map((review, index) => (
            <ReviewCard key={`${review.id}-${index}`} review={review} />
          ))}
        </div>
      </div>
    </div>
  );
} 